.enter-btn{
    color:white;
    background-color:black;
    font-size:2rem;
    padding:0.5% 3%;
    border-radius: 20px;
    margin-top:1%;
}

.first-container{
    text-align: center;
    min-height: 100vh;
    align-content: center;
}

.first-container h1{
    font-size: 3rem;
}

.enter-btn:hover{
    background-color: white;
    transition: 0.3s;
    cursor:pointer;
}


/* *********************************Second Page  */

.second-container{
    text-align: center;
    min-height: 100vh;
    align-content: center;
    font-size: 2rem;
}

.second-container .button1{
    font-size: 3rem;
    padding:1rem;
    margin-inline: 0.5rem;
}

.random{
    align-items: center;
    padding:1% 0;
}

.second-container .button2{
    font-size: 3rem;
    padding:1rem;
    color:salmon;
    background-color: black;
}

.second-container button{
    cursor:pointer;
    border-radius: 10px;
}

.p{
    font-size: 1rem;
    margin-top:20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding:0 10%;
}

.unofficial-container{
    display:flex;
    flex-basis: auto;
}

.first, .second{
    width:50%;
}

.first button{
    background-color: white;
    font-size: 2rem;
    padding:0 3%;
}

.first button:hover{
    color:salmon;
    background-color: black;
    transition: 0.3s;
}

.hide{
    display:none;
}

.yes{
    color:white;
    background-color: black;
    font-size: 3rem;
    padding:1rem;
    margin-inline: 0.5rem;
}

.no{
    color:black;
    background-color: white;
    font-size: 3rem;
    padding:1rem;
    margin-inline: 0.5rem;
}

@media(max-width:980px){
    .first button{
        margin-bottom:10%;
    }
    .first p{
        font-size: 2rem;
        padding:4% 0;
    }
}